	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Vocabulary","date":"03 Mar 2002","slug":"/2002-03-03-vocabulary/","tags":"Y2002"},"html":"<p>Apple. Baby. Splish-splash. Mama. Ja. Hause. Phone. Milk. Nein. On, on, on. Bear. Home. <a href=\"http://dict.leo.org/?search=heia\">Heia</a>. Papa. (Ba)nana. Essen. <a href=\"/2002/01/21\">Daddle</a>. Hammer. No. Oma. Ni-night.  </p>\n<p><a href=\"http://www.orions.ad.jp/urls/word/o/h/ta.html\">Ohda</a>.</p>"},"next":{"fields":{"title":"Adventure Travel","date":"02 Mar 2002","slug":"/2002-03-02-adventure-travel/"}},"previous":{"fields":{"title":"NYC Firefighters at Sylt","date":"05 Mar 2002","slug":"/2002-03-05-nyc-firefighters-at-sylt/"}}},{"node":{"fields":{"title":"Adventure Travel","date":"02 Mar 2002","slug":"/2002-03-02-adventure-travel/","tags":"Y2002"},"html":"<p>My folks are not only going to venture to the wilds of Las Vegas next month to see us, but they're now planning a trip to Europe this August/September with Christopher Ryan as the main attraction. They've already seen more of Europe than I have, but now they are going to take a chartered bus trip through Scandinavia, which starts in London and passes right through Hamburg.</p>\n<p>Since they are going to see us after the bus trip, they are flying into Hamburg instead of London, and will join the bus tour here. Of course, their tour ends in London, so the interesting part will be to get them from London back here. Air travel in Europe is anything but cheap, but the no-frills <a href=\"http://www.ryanair.com\">RyanAir</a> has a one-way deal London-Hamburg for &pound;29.99.</p>\n<p>Actually, you have to include the parentheses: the flight is from London (Stansted) to Hamburg (L端beck). Stansted is 56 km from central London, but one ca get there by bus or train. But saying Hamburg (L端beck) is like saying San Francisco (Sacramento) or New York (Philadelphia). It's a ways out. But for &pound;29.99 noone can complain.</p>\n<p><a href=\"http://www.airwise.com/airports/europe/STN/STNtran2.html\">How to get to London Stansted</a></p>"},"next":{"fields":{"title":"Clear Head","date":"01 Mar 2002","slug":"/2002-03-01-clear-head/"}},"previous":{"fields":{"title":"Vocabulary","date":"03 Mar 2002","slug":"/2002-03-03-vocabulary/"}}},{"node":{"fields":{"title":"Clear Head","date":"01 Mar 2002","slug":"/2002-03-01-clear-head/","tags":"Y2002"},"html":"<p>It's getting scarey. My sleep pattern is so messed up that I am thinking most clearly between 3:30 and 5:00 am.</p>\n<p><a href=\"http://scriptingnews.userland.com/backissues/2002/02/28#lb5c2560d5f14fea0ce8d343fe1c158ca\">Dave doesn't like</a> where the <a href=\"http://www.google.com\">Google home page</a> is going. Funny, I use Google all the time, but almost never use their home page. I never even <a href=\"http://scriptingnews.userland.com/backissues/2002/02/24#l4b56d78e63170cdd6c8b8f9b5e3f7e06\">noticed the Olympic animals</a>. The <a href=\"http://www.mozilla.org\">browsers</a> <a href=\"http://galeon.sourceforge.net/\">I</a> <a href=\"http://www.konqueror.org/\">use</a> <a href=\"http://www.opera.com/\">all</a> have a panel or field to enter a search for Google, so I don't even need a <a href=\"http://www.google.com/palm\">lightweight version</a>.</p>"},"next":{"fields":{"title":"Who's Boss In The EU?","date":"28 Feb 2002","slug":"/2002-02-28-whos-boss-in-the-eu/"}},"previous":{"fields":{"title":"Adventure Travel","date":"02 Mar 2002","slug":"/2002-03-02-adventure-travel/"}}}],"pathPrefix":"","first":false,"last":false,"index":919,"pageCount":1101}}

